import clTabs from '../components/cl-tabs/cl-tabs.vue'
export default {
	components:{
		clTabs
	},
	data() {
		return {
			tabBars:['最新','新闻','图片','视频','推荐','收藏','关注','123','333','555'],
			tabCurrentIndex:-1,
			sysWidth:0,
			source:'touch',
			fristTouch:false
		}
	},
	onLoad() {
		this.sysWidth = uni.getSystemInfoSync().screenWidth

		this.fristTouch = true

	},
	mounted() {
		
		this.tabCurrentIndex = 0
	},
	methods: {
		tabChange(e){
			
			this.tabCurrentIndex = e
			
		},
		changeCurrent(e){
			this.source = e.detail.source
			
		},
		transition(e){
			if(this.source==''){
				
			}else{
				if(this.fristTouch){
					this.$refs.tabs0.move(e.detail.dx-this.tabCurrentIndex*this.sysWidth)
				}else{
					this.$refs.tabs0.move(e.detail.dx)
				}
			}
		},
		animationfinish(e){
			
			this.fristTouch = false
			this.source = 'touch'
			this.tabCurrentIndex = e.detail.current
		}
	}
}
